.homed-container {
    background: #333333;
    min-height: 800px;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
   
    padding-top: 20px;
    padding-bottom: 20px;
    position: relative; /* Necessary for absolute positioning of rings */
    overflow: hidden;
}

/* Add red rings */
.homed-container::before {
    content: "";
    position: absolute;
    top: 2%;
    left: -8%;
    width: 30vh;
    height: 30vh;
    background-image: radial-gradient(circle, transparent 45%, #E35F5F 70%, #E35F5F 80%, #E35F5F 80%, #E35F5F 80%);
    border-radius: 50%;
}

.homed-container::after {
    content: "";
    position: absolute;
    top: 1%;
    left: 55%;
    width: 130vh;
    height: 130vh;
    background-image: radial-gradient(circle, transparent 55%, #E35F5F 70%, #E35F5F 80%, #E35F5F 80%, #E35F5F 80%);
    border-radius: 50%;
  
}



.homeds {
    padding: 4rem;
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    position: relative;
}

.homed__title {
    color: #fff;
    text-align: center;
}

.homed > h2 {
    color: #FFF;
    text-align: center; 
    margin-bottom: 30px;
}

.homed__container {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    max-width: 80%;
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
}

.homed__items {
    display: flex;
    overflow-x: auto;
    padding: 1rem; /* Add padding to prevent the arrows from overlapping with cards */
}

.homed__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
}

.homed__arrow-left {
    position: absolute;
    top: 50%; /* Position the arrow vertically at the center */
    left: 100px; /* Adjust the left distance as needed */
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
}

.homed__arrow-right {
    position: absolute;
    top: 50%; /* Position the arrow vertically at the center */
    right: 100px; /* Adjust the right distance as needed */
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
}

.homed__items-container {
    overflow-x: auto; /* Enable horizontal scrolling */
    max-width: 100%; /* Ensure the container takes the full width */
}

.homed__item {
    flex: 0 0 auto; /* Prevent cards from stretching */
    margin: 0 0.5rem; /* Adjust margin between cards */
    width: 600px; /* Set the width of each card */
    height: 400px; /* Set the height of each card */
    border-radius: 25px; /* Add rounded corners */
}

.homed__item__link {
    width: 100%;
}

.homed__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
    border-radius: 15px; /* Add rounded corners */
}

.fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
}

.homed__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
}

.homed__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
}

.homed__item__img:hover {
    transform: scale(1.1);
}

.homed__item__info {
    padding: 20px 30px 30px;
}

.homed__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
}

@media only screen and (max-width: 1024px) {
    .homed__item {
        margin-bottom: 2rem;
    }
}

