
.footer-container-B {
    background-color: black;
    
    padding: 2rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer-subscription-B {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 24px;
    padding: 24px;
    color: #fff;
    
}

.footer-subscription-B > p {
    
}

.footer-subscription-heading-B {
    margin-bottom: 24px;
    font-size: 24px;
}

.footer-subscription-text-B {
    margin-bottom: 24px;
    font-size: 20px;
    
}

.footer-input-B {
    padding: 8px 20px;
    border-radius: 2px;
    margin-right: 10px;
    outline: none;
    border: none;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid #fff;
  
}

.footer-links-B {
    width: 100%;
    max-width: auto;
    display: flex;
    justify-content: center;
    
  
}

.footer-link-wrapper-B {
    display: flex;
    margin-right: -14rem;
    margin-left: 150px;
   
}

.footer-link-items-B {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 0px;
    text-align: left;
    width: 560px;
    box-sizing: border-box;
    font-size: 12px;
    
   
}

.footer-link-items-B h2 {
    margin-bottom: 16px;
    font-family:'Poppins',sans-serif;
  
    
}

.footer-link-items-B > h2 {
    color: #fff;
}

.footer-link-items-B a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 0.5rem;
}

.footer-link-items-B a:hover {
    color: #e9e9e9;
    transition: 0.3s ease-out;
}

.footer-email-form-B h2 {
    margin-bottom: 2rem;
}

.footer-input-B::placeholder {
    color: #b1b1b1;
}

/* Social Icons */
.social-icon-link-B {
    color: #fff;
    font-size: 24px;
}

.social-media-B {
    max-width: 100%;
    width: 100%;
}

.social-media-wrap-B {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 100%;
    margin: 30px auto 0 auto;
}

.social-icons-B {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
}

.social-logo-B {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
}

.website-rights-B {
    color: #fff;
    margin-bottom: 0px;
    justify-content: center;
    text-align: 'center';
    float: center;
    margin-left: 45%;
}

@media screen and (max-width: 820px) {
    .footer-links-B {
      padding-top: 2rem;
    }

    .footer-input-B {
      width: 100%;
    }

    .btn-B {
      width: 100%;
    }

    .footer-link-wrapper-B {
      flex-direction: column;
    }

    .social-media-wrap-B {
      flex-direction: column;
    }
}

@media screen and (max-width: 768px) {
}
.horizontal-line {
    width: 100%;
    height: 2px; /* Adjust the height as needed */
    background-color: #E34b4b; /* Color of the line */
    margin-top: 2%;
  }