.team.image {
    object-fit: cover;
    width: 100%;

    position: fixed;
    z-index: -1;
}
.team.homecs {
    padding: 4rem;
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    position: relative;
}
.team.hero-container {
    background: url('../About Us.jpeg') center center/cover no-repeat;
    height: 80vh;
    width: 100%;
    padding-right: 30%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
    z-index: 0;
} 

.team.hero-container > h1 {
    position: relative;
    color: #E34B4B;
    font-size: 60px;
    text-align: left; 
    padding-bottom: 2%;
    margin-top: -20%;
}

.team.hero-container > h2 {
    display: flex;
    color: #FFF;
    font-size: 60px;
    text-align: left; 
}

.team.hero-container > p {
    color: #FFF;
    font-size: 18px;
    text-align: left; 
    margin: 0 auto; 
    margin-right: 50%;
}

.team.hero-btns {
    margin-top: 5%; /* Adjust the distance from the top as needed */
    margin-left: 20px; /* Adjust the distance from the left as needed */
   width: 120%;
   z-index: 1;
}
.white-text {
    color: #FFF; /* White color */
}