.GovtB-container {
    background: #333333;
    height: 50vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    overflow: hidden;
}

.GovtB-container > h1 {
    color: #E34B4B;
    font-size: 48px;
    margin: 0 auto; 
    margin-top: 5%;
    margin-bottom: -5%;;
    text-align: center;
}

.Govtcontent-wrapperB {
    background-color: #4e4e4e;
    padding: 50px;
    border-radius: 50px;
    width: 80%;
    height: 60%;
    margin: 5% auto 0;
    display: flex;
    justify-content: space-between; 
    align-items: center;
    float: center;
}

.GovtB-section {
    display: inline-block; 


}

.GovtB-subtitle {
    color: white;
    font-size: 22px;
   margin-top: 25px;
    margin-bottom: 20px;
}

.GovtB-learn-more {
    position: relative;
    color: #E34B4B;
    font-size: 16px;
    font-weight: bold;
    float: center;
}

.GovtB-learn-more i {
    margin-left: 5px;
}
.GovtB-section:not(:last-child) {
    border-right: 1px solid #fff;
    height: 100%;
    padding-right: 60px; /* Adjust spacing between sections and vertical lines */
}