.image {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.aboutusc-container {
    background: #333333;
    height: 40vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 90px;
    padding-right: 50px;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}

.aboutusbs {
    padding: 4rem;
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    position: relative;
}

.aboutusc__title {
    color: #fff;
    text-align: center;
}

.aboutusc > h2 {
    color: #FFF;
    text-align: center; 
    margin-bottom: 30px;
}

.aboutusc__container {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    max-width: 1240px;
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
}

.aboutusc__items {
    display: flex;
    overflow-x: auto;
    padding: 1rem; /* Add padding to prevent the arrows from overlapping with cards */
}

.aboutusc__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
}

.aboutusc__arrow-left {
    position: absolute;
    top: 50%; /* Position the arrow vertically at the center */
    left: 100px; /* Adjust the left distance as needed */
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
}

.aboutusc__arrow-right {
    position: absolute;
    top: 50%; /* Position the arrow vertically at the center */
    right: 100px; /* Adjust the right distance as needed */
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
}

.aboutusc__items-container {
    overflow-x: auto; /* Enable horizontal scrolling */
    max-width: 100%; /* Ensure the container takes the full width */
}

.aboutusc__item {
    flex: 0 0 auto; /* Prevent cards from stretching */
    margin: 0 0.5rem; /* Adjust margin between cards */
    width: 600px; /* Set the width of each card */
    height: 400px; /* Set the height of each card */
    border-radius: 25px; /* Add rounded corners */
}

.aboutusc__item__link {
    width: 100%;
}

.aboutusc__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
    border-radius: 15px; /* Add rounded corners */
}

.fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
}

.aboutusc__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
}

.aboutusc__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
}

.aboutusc__item__img:hover {
    transform: scale(1.1);
}

.aboutusc__item__info {
    padding: 20px 30px 30px;
}

.aboutusc__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
}

@media only screen and (max-width: 1024px) {
    .aboutusc__item {
        margin-bottom: 2rem;
    }
}

.aboutusc-container > h1 {
    color: #E34B4B;
    font-size: 42px;
    text-align: left; 
}

.aboutusc-container > h2 {
    color: #FFF;
    font-size: 45px;
    text-align: right; 
    margin-top: 5%;
    margin-left: 40%;

}

.aboutusc-container > p {
    color: #FFF;
    font-size: 22px;
    text-align: left; 
    margin: 0 auto; 
    margin-right: 5%;
    margin-top: 2%;
}

.aboutusc-btn {
    display: flex;
    justify-content: center; /* Horizontally center the button */
    align-items: center; /* Vertically center the button */
    margin-top: 20px; 
}

.aboutusc-container img {
    width: 250px; /* Set width to 500px */
    height: 250px; /* Set height to 500px */
    border-radius: 10%; /* Apply rounded borders */
    float: left; /* Align the image to the left */
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 40px; /* Add margin for spacing */
    margin-left: 20px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2); /* Add slight shadow */
}

.text-wrapper3 {
    /* Styles for the text wrapper */
    width: 100%;
}

.text-wrapper3 > h1 {
    color: #E34B4B;
    font-size: 22px;
    text-align: left; 
    float: center;
    margin-top: 100px;

}

.text-wrapper3 > h2 {
    color: #FFF;
    font-size: 60px;
    text-align: left; 
}

.text-wrapper3 > p {
    color: #FFF;
    font-size: 22px;
    text-align: left; 
    float: center;
    margin: 0 auto; 
    margin-right: 10%;
}
