.image {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.aboutusb-container {
    background: #333333;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 4%;
    padding-bottom: 4%;
    padding-left: 4%;
    padding-right: 4%;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
    display: flex;
  


}
.border {
    border: 2px solid #E35F5F;
    border-radius: 5%;
    height: 100%;
    width: 100%;
   
    
}

.aboutusbs {
    padding: 4rem;
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    position: relative;
}

.aboutusb__title {
    color: #fff;
    text-align: center;
}

.aboutusb > h2 {
    color: #FFF;
    text-align: center; 
    margin-bottom: 30px;
}

.aboutusb__container {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    max-width: 100%;
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
}

.aboutusb__items {
    display: flex;
    overflow-x: auto;
    padding: 1rem; /* Add padding to prevent the arrows from overlapping with cards */
}

.aboutusb__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
}

.aboutusb__arrow-left {
    position: absolute;
    top: 50%; /* Position the arrow vertically at the center */
    left: 100px; /* Adjust the left distance as needed */
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
}

.aboutusb__arrow-right {
    position: absolute;
    top: 50%; /* Position the arrow vertically at the center */
    right: 100px; /* Adjust the right distance as needed */
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
}

.aboutusb__items-container {
    overflow-x: auto; /* Enable horizontal scrolling */
    max-width: 100%; /* Ensure the container takes the full width */
}

.aboutusb__item {
    flex: 0 0 auto; /* Prevent cards from stretching */
    margin: 0 0.5rem; /* Adjust margin between cards */
    width: 600px; /* Set the width of each card */
    height: 400px; /* Set the height of each card */
    border-radius: 25px; /* Add rounded corners */
}

.aboutusb__item__link {
    width: 100%;
}

.aboutusb__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
    border-radius: 15px; /* Add rounded corners */
}

.fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
}

.aboutusb__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
}

.aboutusb__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
}

.aboutusb__item__img:hover {
    transform: scale(1.1);
}

.aboutusb__item__info {
    padding: 20px 30px 30px;
}

.aboutusb__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
}

@media only screen and (max-width: 1024px) {
    .aboutusb__item {
        margin-bottom: 2rem;
    }
}

.aboutusb-container > h1 {
    color: #E34B4B;
    font-size: 60px;
    text-align: left; 
}

.aboutusb-container > h2 {
    color: #FFF;
    font-size: 60px;
    text-align: left; 
}

.aboutusb-container > p {
    color: #FFF;
    font-size: 22px;
    text-align: left; 
    margin: 0 auto; 
    margin-right: 30%;
}

.aboutusb-btns {
    margin-top: 20px; /* Adjust the distance from the top as needed */
    margin-left: 20px; /* Adjust the distance from the left as needed */
}

.aboutusb-container img {
    width: 55vh; /* Set width to 500px */
    height: 60vh; /* Set height to 500px */
    border-radius: 8%; /* Apply rounded borders */
    float: left; /* Align the image to the left */
    margin-top: 2%;
    margin-bottom: 12%;
  /* Add margin for spacing */
    margin-left: 12%;
    margin-right: 10%;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2); /* Add slight shadow */
}

.text-wrapper2 {
    /* Styles for the text wrapper */
    width: 100%;
   
    overflow: visible;
}

.text-wrapper2 > h1 {
    color: #E34B4B;
    font-size: 60px;
    text-align: left; 
    float: center;
    margin-top: 100px;

}

.text-wrapper2 > h2 {
    color: #FFF;
    font-size: 60px;
    text-align: left; 
}

.text-wrapper2 > p {
    color: #FFF;
    font-size: 22px;
    text-align: left; 
    float: center;
    margin: 0 auto; 
    margin-right: 10%;
}
.border > h1 {
    color: white;
    font-size: 42px;
    text-align: left; 
    margin-top: 2%;
    float: center;
}


  
 .popup {
  position:fixed;
  top:  0%;
  left: 0%;
  width: 100%;
  height: 200%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: visible;
  overflow-y: scroll;
  

 
  z-index: 1;
  
}

.popup-inner {
    background-color: #202020;
 
    overflow: visible;
    border-radius: 35px;
    max-width: 100%;
    max-height: 100%;
    position: absolute; 
    width: 32%; /* Set the width of the pop-up */
    height: auto; /* Set the height of the pop-up */
    
 overflow-y: scroll;
 top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

}
.popup-inner > p{
    padding: 20px;
    line-height: 2rem;
}


.popup img {
    width: 100%;
    height: auto;
    object-position: top;
    margin-left: 0%;
    margin-top: -3.5%;
    border-radius: 35px 35px 10px 10px;
    display: flex;
    margin-bottom: 3%;
  
  }
  
  .close-btn {
    position: absolute;
    top: 2px;
    right: 25px;
    z-index: 1; /* Ensure the button appears above the image */
    background-color: transparent;
    border: none;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .close-btn:hover {
    color: #E34B4B;
  }

.popup p {
  margin-top: 2%;
  color: #E34B4B;
  font-size: 24px;
 
}

.block-scroll {
    overflow: hidden;
  }

  .popup-button {
    position: absolute;
    bottom: 10px;
    left: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 14px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .popup-button:hover {
    background-color: rgba(0, 0, 0, 0.7);
  }
  .clickable-image {
  float: left;
  margin: 10px;
  cursor: pointer;
}