.image {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.homec-container {
    position: relative;
    min-height: 800px;
    height:'80vh';
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 50px;
    background: 
    linear-gradient(to bottom, #333333 10%, #E35F5F 10%, #E35F5F 90%, #333333 90%), 
    linear-gradient(to right, #333333 10%, #E35F5F 10%, #E35F5F 90%, #333333 90%);
    z-index: -1;
}

.homec-container::before,
.homec-container::after {
    content: '';
    position: absolute;
    z-index: -1;
    left: 0;
    right: 0;
    height: 10%;
    background: #333333;
}

.homec-container::before {
    top: 3%;
    border-radius: 50% 100% 0 0;
    background-color: #E35F5F;
}

.homec-container::after {
    bottom: 0;
    border-radius: 0 0 100% 0;
    background-color: #E35F5F;
}

.homecs {
    padding: 4rem;
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    position: relative;
}

.homec__title {
    color: #fff;
    text-align: center;
}

.homec > h2 {
    color: #FFF;
    text-align: center;
    margin-bottom: 30px;
}

.homec__container {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    max-width: 1240px;
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
}

.homec__items {
    display: flex;
    overflow-x: auto;
    padding: 1rem;
}

.homec__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
}

.homec__arrow-left {
    left: 100px;
}

.homec__arrow-right {
    right: 100px;
}

.homec__items-container {
    overflow-x: auto;
    max-width: 100%;
}

.homec__item {
    flex: 0 0 auto;
    margin: 0 0.5rem;
    width: 600px;
    height: 400px;
    border-radius: 25px;
}

.homec__item__link {
    width: 100%;
}

.homec__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
    border-radius: 15px;
}

.fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
}

.homec__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
}

.homec__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
}

.homec__item__img:hover {
    transform: scale(1.1);
}

.homec__item__info {
    padding: 20px 30px 30px;
}

.homec__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
}

@media only screen and (max-width: 1024px) {
    .homec__item {
        margin-bottom: 2rem;
    }
}

@media only screen and (max-width: 768px) {
    .homec-container {
        padding: 20px;
    }

    .homec-container > h1 {
        font-size: 36px;
        padding-left: 0;
        padding-right: 0;
    }

    .homec-container img {
        width: 100%;
        height: auto;
        margin-top: 20px;
        margin-left: 0;
    }

    .homec-container ul {
        padding-left: 0;
        font-size: 18px;
    }

    .homec-container p {
        font-size: 18px;
        margin-right: 0;
    }

    .text-wrapper > p {
        font-size: 16px;
        margin-right: 0;
    }

    .homec__arrow-left,
    .homec__arrow-right {
        font-size: 1.5rem;
        left: 20px;
        right: 20px;
    }

    .homec__item {
        width: 100%;
        height: auto;
    }
}

@media only screen and (max-width: 480px) {
    .homec-container > h1 {
        font-size: 24px;
    }

    .homec-container p {
        font-size: 16px;
    }

    .text-wrapper > p {
        font-size: 14px;
    }

    .homec__arrow-left,
    .homec__arrow-right {
        display: none;
    }
}

.homec-container > h1 {
    color: #FFF;
    font-size: 48px;
    text-align: center;
    padding-left: 7.5%;
    padding-right: 7.5%;
}

.homec-container > h2 {
    color: #FFF;
    font-size: 60px;
    text-align: left;
}

.homec-container > p {
    color: #FFF;
    font-size: 22px;
    text-align: left;
    margin: 0 auto;
    margin-right: 30%;
}

.homec-btns {
    margin-top: 20px;
    margin-left: 20px;
}

.homec-container img {
    width: 650px;
    height: 500px;
    border-radius: 10%;
    float: left;
    margin-top: 5%;
    margin-right: 0px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
    margin-left: 5%;
}

.text-wrapper {
    width: 100%;
    overflow: scroll;
}

.text-wrapper > h1 {
    color: #FFF;
    font-size: 3rem;
    text-align: left;
    margin-top: 100px;
}

.text-wrapper > h2 {
    color: #FFF;
    font-size: 3rem;
    text-align: left;
}

.text-wrapper > p {
    color: #FFF;
    font-size: 1.75rem;
    text-align: left;
    margin: 0 auto;
    margin-right: 10%;
    padding-top: 4%;
}
