.image-sol {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
  }
  
  .homecs-sol {
    padding: 4rem;
    position: relative;
  }
  
  .hero-container-sol {
    position: relative; /* Changed */
    height: 80vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 15%;
    padding-left: 10%;
    
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);

  
  }
  
  .hero-image { /* Added */
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: -1;
 
    
  }
  
  .hero-container-sol > h1 {
    color: #E34B4B;
    font-size: 60px;
    text-align: left;
  }
  
  .hero-container-sol > h2 {
    color: #FFF;
    font-size: 60px;
    text-align: left;
  }
  
  .hero-container-sol > p {
    color: #FFF;
    font-size: 18px;
    text-align: left;
    margin: 0 auto;
    margin-right: 50%;
  }
  
  .hero-btns-sol {
    margin-top: 20px;
    margin-left: 20px;
  }
  