.cards2 {
    background: #333333;
    position: relative;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    padding-top: 1%;
}

.cards2__title {
    color: #fff;
    text-align: center;
}

.cards2 > h2 {
    color: #FFF;
    text-align: center; 
    margin-bottom: 30px;
}

.cards2__container {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    max-width: 1240px;
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
    background: #333333;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);

}

.cards2__items {
    display: flex;
    overflow-x: auto;
    padding: 1rem; /* Add padding to prevent the arrows from overlapping with cards */
    
 
}


.cards2__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 2rem;
    color: black;
    cursor: pointer;
}

.cards2__arrow-left {
    position: absolute;
    top: 50%; /* Position the arrow vertically at the center */
    left: 100px; /* Adjust the left distance as needed */
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
  }
  
  .cards2__arrow-right {
    position: absolute;
    top: 50%; /* Position the arrow vertically at the center */
    right: 100px; /* Adjust the right distance as needed */
    transform: translateY(-50%);
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
  }

.cards2__items-container {
    overflow-x: scroll; /* Enable horizontal scrolling */
    max-width: 100%; /* Ensure the container takes the full width */
    max-height: 100%;
    overflow: auto;
}

.cards2__item {
    flex: 0 0 auto; /* Prevent cards from stretching */
    margin: 0 1.5rem; /* Adjust margin between cards */
    width: 300px; /* Set the width of each card */
    height: 350px; /* Set the height of each card */
    border-radius: 10px; /* Add rounded corners */
    background-color: #333333;
}


.cards2__item__pic-wrap {
    position: relative;
    width: 100%;
    height: 100%;

    padding: 5%;
    overflow: hidden;
    border-radius: 10px; /* Add rounded corners */
    background-color: rgb(4, 4, 4); /* Ensure image background color is white */
    
  
}

.fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
}

.cards2__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: black;
    box-sizing: border-box;
}



